.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.container {
  position: fixed;
  width: 100vw;
  height: 100vh;

  .background {
    position: relative;
    width: auto;
    min-width: 100%;
    height: 100%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  .picture {
    position: absolute;
    width: auto;
    height: 90%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);

    &.mobile {
      width: 100%;
      height: auto;
      max-height: 100vh;
    }
  }
}
